import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../view/home'
import blog from '../view/blog'
import about from '../view/about.vue'
import project from '../view/project.vue'
import liuyan from '../view/liuyan.vue'
import detail from '../view/blog/detail.vue'

Vue.use(VueRouter); // Vue.use(插件)  在Vue中安装插件
// const originalPush = VueRouter.prototype.push
// VueRouter.prototype.push = function push(location) {
//         return originalPush.call(this, location).catch(err => err)
//       }

const router = new VueRouter({
  // 路由配置
  routes: [ // 路由规则
    // 当匹配到路径 /foo 时，渲染 Foo 组件
    { name:'home' ,path: '/index.html', component: home },
    // 当匹配到路径 /bar 时，渲染 Bar 组件
    { name: 'blog', path: '/blog', component: blog },
    { name: 'cateblog', path: '/blog/cate/:cate', component: blog },
    {name:'detail',path:'/blog/:id',component:detail},
    { name: 'about', path: '/about', component: about },
    { name: 'project', path: '/project', component: project },
    { name: 'liuyan', path: '/liuyan', component: liuyan },
  ],
  mode: "history",
})

export default router;