<template>
<div class='index-container' ref="container" @wheel="handlewheel">
   <loading class="loading" v-if="loaded"></loading>
  <ul class="lunbo" :style="{marginTop}" @transitionend="handletran" >
    
    <li v-for='item in banners' :key="item.id"   >
    <lunbo :src="item.bigImg" :title="item.title" :text="item.description"></lunbo>
    
</li>

  </ul>
  


<div @click="goto(index-1)"><iccon type="up" class="up"  v-show="index>0"></iccon></div>
  
   <div @click="goto(index+1)"><iccon type="down" class="down"  v-show="index<banners.length-1"></iccon></div>
<div class="right-yuandian">
  <li :class="{active:i===index} " v-for="(item,i) in banners" :key="item.id"  @click="index=item.id-1">
</li>
</div>


</div>
</template>

<script>
import  {getBanners} from '@/ajax/axios.js'
import lunbo from './lunbotu';
import iccon from '@/componets/icon.vue'
import loading from '@/componets/loading'

// import '../ajax/axios'

export default {
  components:{
     lunbo,
     iccon,
     loading,
     
  },
  data(){
    return{
      banners:[],
      index:0,
      height:0,
      swing:false,
      loaded:true
    }
  },
  async created(){
    this.banners=await getBanners()

    this.loaded=false
  },
  mounted(){
   this.height=this.$refs.container.clientHeight;
   window.addEventListener('resize',this.handleresize)
  },
  destoryed(){
    window.removeEventListener('resize',this.handleresize)
  },
  computed:{
marginTop(){
return -this.index*this.height+"px";
}
  },
  methods:{
    goto(i){
      this.index=i;
      // console.log('ss')
    },
    handlewheel(e){
      if(this.swing ){
        return
      }
      
      if(e.deltaY<-10  && this.index>0 ){
      this.index--;
      this.swing=true;
      //  this.swing=false;
      }else if(e.deltaY>10 && this.index<this.banners.length-1){
        this.index++
        this.swing=true;
        // this.swing=false;
      }
    },
    handletran(){
      this.swing=false;
      
    },
    handleresize(){
this.height=this.$refs.container.clientHeight;
    }
  }
}
</script>
<style scoped lang="less">
@import '~@/style/all.less';
@import '~@/style/global.less';
.index-container{
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  .loading{
    .self-center()
  }
.up{
    cursor: pointer;
    .self-center();
   
    transform: translateX(-50%);
    // left:60%;
    // position: absolute;
    top:15px;
    color:@gray; 
    transition: top .5s;
    &:hover{
      top:10px
    }
  }
  .down{
    .self-center();
    cursor: pointer;
  //  position: absolute;
   transform: translateX(-50%);
    top:auto;
    bottom: 16px;
    color:@gray; 
    // left:60%;
    // transform: translateX(-9px);
    transition: bottom .5s;
    &:hover{
      bottom:10px
    }
  }
  .right-yuandian{
.self-center();
    cursor: pointer;
     transform: translateY(-50%);

     left: auto;
    right: 10px;
    li{
      margin:5px 0;
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color:@LightSlateGray;
    opacity: 0.5;
    &.active{
      background-color:white;
    }
    }
  }
  .lunbo{
     transition: 500ms;
    width: 100%;
    height: 100%;
    // margin-top: -100%;
    li{
    width: 100%;
    height: 100%;
    }
    
  }
  
}
</style>