<template>
<div class='fenlei-container'>
  <loading class="loading"  v-if="loaded"></loading>
  <rightlist :listArray="list" @toact="toselect" >
    
  </rightlist>
</div>
</template>

<script>
import  {getblogTypes} from '@/ajax/axios.js'
import loading from '@/componets/loading'
import rightlist from './List'
export default {
  components:{
    loading,
    rightlist
  },
  data(){
    return{
      data:[],
      loaded:true
    }
  },
  async created(){
 
    this.data=await getblogTypes()
   this.loaded=false
  },
  methods:{
  toselect(item){
  
     const query={
            page:1,
            limit:this.limit
          }
        if(item.id==-1){
          
          this.$router.push({
            name:"blog",
            // query
          })
        }else{
          this.$router.push({
            name:'cateblog',
            query,
            params:{
              cate:item.id
            }
          })
        }
  }
  },
  computed:{
    cate(){
      return +this.$route.params.cate ||-1;
    },
    limit(){
      return +this.$route.query.limit ||20;
    },
    list(){
      const alltitle=this.data.reduce((a,b)=>a+b.articleCount,0)
      
      const all=[

        {name:'全部',id:-1,articleCount:alltitle},
        ...this.data
        ]

      

        return all.map(it=>({
          ...it,
          isactive:it.id==this.cate
        }));
    }
  }
}
</script>
<style scoped lang='less'>
@import "~@/style/global.less";
@import '~@/style/all.less';
.fenlei-container{
  position: relative;
  width: 100%;
  height: 100%;
  padding: 20px;
  padding-bottom: 20px;
  .loading{
    .self-center()
  }
}
</style>