<template>
<div class='detaillist-container'>
  <rightlist :listArray="toc" class="list" @toact="hadle"></rightlist>
</div>

</template>

<script>
import rightlist from './List'
export default {
  components:{
    rightlist
  },
  props:{
    toc:{
      type:Array,
    }
  },
  methods:{
    hadle(item){
      
      location.hash=item.anchor
    }
  }
}
</script>
