<template>
<div class='list-container'>
<ul>
  <li v-for="(item,i) in listArray"  :key="i" >
   <span  @click="toactive(item)" :class="{active:item.isactive}">{{item.name}}<span style="margin-left:20px">{{item.articleCount}}</span></span>
   <rightlist :listArray="item.children" @toact="toactive" class="child" ></rightlist>
  </li>
</ul>
</div>
</template>

<script>

export default {
   name:'rightlist',
  props:{
    listArray:{
      type:Array,
      default:()=>[]
    }
  },
  methods:{
    toactive(item){
      // if(!item.isactive){
      //  item.isactive=true;
      this.$emit('toact',item)
      // }
      
      //点击的哪个对象传给父组件
    }
  },
  
}
</script>
<style scoped lang='less'>
@import '~@/style/global.less';
.child{
  margin-left: 20px;
  
}
.list-container{
  overflow-y:scroll ;
  width: 100%;
  height: 100%;
  li{
    font-size: 14px;
    cursor: pointer;
    list-style: none;
    margin: 10px 0 10px 0px;
    .active{
       color:@warn;

  }
  }
  
}
</style>