<template>
<div class="blogs-container" ref="container">
  <loading class="loading"  v-if="loaded"></loading>
    <ul>
      <li v-for="item in data.row" :key="item.id">
        <div class="thumb" v-if="item.img">
          <router-link :to="{
            name:'detail',
            params:{
              id:item.id
            }
          }">
            <img
              :src="item.img"
              :alt="item.title"
              :title="item.title"
            />
          </router-link>
        </div>
        <div class="main">
          <router-link :to="{
            name:'detail',
            params:{
              id:item.id
            }
          }" >
            <h2>{{item.title}}</h2>
          </router-link>
          <div class="aside">
            <span>日期：{{item.date}}</span>
            <span>浏览：{{item.scan}}</span>
            <span>评论{{item.pinglun}}</span>
            <router-link :to="{
              name:'cateblog',
              params:{
                cate:item.fenlei.id
              }
            }" class="">{{item.fenlei.name}}</router-link>
          </div>
          <div class="desc">
           {{item.duanluo}}
          </div>
        </div>
      </li>
      
    </ul>
    <!-- 分页放到这里 -->
    <pager v-if="data.total"  @pagerChange='change($event)' :total="data.total" :limit="routeInfo.limit" :current="routeInfo.page"/>


  </div>
</template>

<script>
import  {getblogs} from '@/ajax/axios.js'
import loading from '@/componets/loading'
// import pager from '@/componets/page'
import pager from '@/componets/Pager1'
export default{
   components:{
    // rightlist
    // layout,
    // blogs,
    // fenlei,
    loading,
    pager
    
  },
  computed:{
    routeInfo(){
      
      const fenleiID =+this.$route.params.cate ||-1;
      const page=+this.$route.query.page || 1;
      const limit=+this.$route.query.limit ||20 ;
      return {
        fenleiID,page,limit
      }
    }
  },
  data(){
    return {
      loaded:true,
      data:[],
      

    }
  },
  
  async created(){
  this.data=await getblogs(this.routeInfo.page,this.routeInfo.limit,this.routeInfo.cate);
  // console.log(this.data.row)
  this.current=this.routeInfo.page

  this.loaded=false;
  },
  mounted(){
   this.$refs.container.addEventListener("scroll",this.handler)
   this.$bus.$on('setScroll',this.handleTop)
  },

  destroyed(){
    this.$bus.$emit('mainScroll')
  },

  methods:{
    handler(){
      this.$bus.$emit('mainScroll',this.$refs.container)
    },
    handleTop(num){
      this.$refs.container.scrollTop=num
    },
    change(n){
        // this.current=n;
        const query={
            page:n,
            limit:this.routeInfo.limit
          }
        if(this.routeInfo.cate==-1){
          
          this.$router.push({
            name:"blog",
            query
          })
        }else{
          this.$router.push({
            name:'cateblog',
            query,
            params:{
              cate:this.routeInfo.cate
            }
          })
        }
      },
  },
  watch:{
   async  $route(newlet,oldlet){
   
      this.loaded=true
     this.$refs.container.scrollTop=0

      this.data=await getblogs();
      this.loaded=false
    }
  }
}
</script>
<style scoped lang='less'>
@import "~@/style/global.less";
@import '~@/style/all.less';

.blogs-container {
  scroll-behavior: smooth;
  .loading{
    .self-center()
  }
  a{
    color: black;
  }
  line-height: 1.7;
  position: relative;
  padding: 20px;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
   
  }
}
li {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid @gray;
  margin: 0px 10px;
  .thumb {
    flex: 0 0 auto;
    margin-right: 15px;
    img {
      display: block;
      max-width: 130px;
      border-radius: 5px;
      
    }
    
  }
  .main {
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
  .aside {
    font-size: 12px;
    color: @gray;
    span {
      margin-right: 15px;
    }
    a{
      color:@gray;
    }
  }
  .desc {
    margin: 15px 0;
    font-size: 10px;
  }
}
</style>