<template>
<div v-show='show' class='top-container' @click="toTop">
  Top
</div>
</template>

<script>

export default {
  data(){
    return {
      show:false
    }
  },
  created(){
    this.$bus.$on('mainScroll',this.handler)
  },
  methods:{
    handler(dom){
      if(!dom){
        this.show=false;
        return
      }
    
      if(dom.scrollTop>=500){
        this.show=true;
      }
      if(dom.scrollTop<500){
        this.show=false
      }
    },
    toTop(){
      
      this.$bus.$emit('setScroll',0)
    }
  }
}
</script>
<style scoped lang='less'>
.top-container{
  background-color: rgb(92, 101, 109);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  z-index: 999;
 right: 50px;
  bottom: 50px;
  text-align: center;
  cursor: pointer;
  line-height: 50px;
}
</style>