<template>
<div class='detail-container'>
  <loading class="loading"  v-if="loaded"></loading>
 <!-- {{$route.params.id}} -->
 
 <layout>
   <template  >

     <div  ref="detail" class="middle"><contents :blog="data"></contents></div>
       
    
   </template>
   <template v-slot:footer >
     <div class="right"><detaillist :toc="data.toc"></detaillist></div>
   </template>
 </layout>
</div>
</template>

<script>
import  {getblog} from '@/ajax/axios.js'
import loading from '@/componets/loading'
import layout from '@/componets/Layout'
import detaillist from './detaillist'
import contents from './detailcontent'


export default {
  components:{
    loading,
    layout,
    detaillist,
    contents
 
  },
   data(){
    return {
      loaded:true,
      data:{},
    }
  },
 async created(){

    this.data=await getblog(1)
    // console.log(this.data);
    this.loaded=false;
  },
  mounted(){
   this.$refs.detail.addEventListener("scroll",this.handler);
   this.$bus.$on('setScroll',this.handleTop)
  },

  destroyed(){
    this.$bus.$emit('mainScroll')
  },
  methods:{
    handler(){
      this.$bus.$emit('mainScroll',this.$refs.detail)
    },
    handleTop(num){
      this.$refs.detail.scrollTop=num
    }
  }
}
</script>
<style scoped lang='less'>
@import "~@/style/global.less";
@import '~@/style/all.less';
.detail-container{
  // position: relative;
  width: 100%;
  height: 100%;
 
.loading{
    .self-center()
  }
  .middle{
    height: 100%;
    width: 100%;
    padding: 5px 4px;
    overflow-y: scroll;
    scroll-behavior: smooth;
     /deep/ li{
       list-style-type: disc;
      margin:15px 35px
    }
    /deep/ img{
      width: 250px;height: 250px;
    }
    /deep/ p{
      font-size: 14px;
      margin:10px 10px
    }
  }
   
  .right{
    width: 200px;
    height: 100%;
  }
}

</style>