<template>
  <i class="iconfont icon-container" :class='fontclass'></i>
</template>

<script>
  const iconmap = {
    home: 'icon-ziyuan62',
    QQ:'icon-tubiao215',
    weixin:'icon-weixin',
    email:'icon-youxiang',
    git:'icon-GitHub',
    index:'icon-ziyuan62',
    wenzhang:'icon-wenzhang',
    aboutme:'icon-guanyuwomen',
    project:'icon-jiaowu',
    liuyan:'icon-liuyanban-05',
    up:'icon-shangjiantou',
    down:'icon-iconfontarrows1'

  }
  export default {
    props: {
      type: {
        type: String
      }
    },
    computed: {
      fontclass() {
        return iconmap[this.type]
      }
    }
  }
</script>
<style scoped>
  @import "//at.alicdn.com/t/font_2493700_7dwhir72a3m.css";


  .iconfont {
    
    font-size: 27px;
  }
</style>